*{
  margin: 0;
  padding: 0;
}
ul{
  margin: 0;
  padding: 0;
}
li{
  list-style: none;
}
body{
  background-color: #eef4f7;
}

.side-menu {
  position: fixed;
  background-color: #eef4f7;
  width: 450px;
  height: 94%;
  box-sizing: border-box;
  padding: 44px 20px;
  overflow-y: scroll;
  transition: width .2s ease-in;
}

.side-menu.inactive {
  width: 4%;
}

.side-menu .top-section {
  position: relative;
  border: 1px solid rgb(255, 255, 255);
  border-radius: 5px;
  background-color: white;
  padding: 12px;
  height: 100%;
}

.menu-name.menu-icon {
  padding: 5px;
}

.side-menu .top-section .logo{
  width: 80px;
  height: 100px;
  overflow: hidden;
  display: inline-block;
}
.side-menu  .top-section .logo img{
  max-width: 100%;
  max-height: 100%;
}
.side-menu .top-section .toggle-menu-btn {
  color: #333;
  font-size: 20px;  
  position: absolute;  
  right: 0;
  transform: translateY(-50%);
  cursor: pointer;
  
}
.side-menu.inactive .top-section .toggle-menu-btn {
  right: -50px;  
}
.side-menu .search-controller input[type=text]{
  border: 0;
  outline: none;
  height: 35px;
  background-color: #333;
  border-radius: 5px;
  display: block;
  margin: 20px 0;
  width: 100%;
  box-sizing: border-box;
  padding-left: 40px;  
  color: #666;
  
}
.side-menu .search-controller {
  color: white;
  position: relative;
}
.side-menu .search-controller .search-btn {
  width: 40px;
  height: 35px;
  position: absolute;
  background-color: transparent;
  border: 0;
  font-size: 20px;
  color: #666;  
}
