.side-menu .main-menu .sub-menu {
  color:  #333;
  margin-left: 20px;
  border-left: 1px solid #666;
  box-sizing: border-box;
  padding-left: 30px;
  max-height: 0;
  overflow: hidden;  
  transition: max-height .2s ease-in;
}

.side-menu .main-menu .sub-menu.active {
  max-height: 200px;
}
.side-menu .main-menu .sub-menu a{
  display: block;
  margin: 30px 0;
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;
  color: #333;
}

.side-menu .main-menu .sub-menu a.active {
  background-color: #666;  
  display: block;
  box-sizing: border-box;
  padding: 5px;
  border-radius: 5px;
  color: white;
}
.side-menu-footer {
  width: 100%;  
  background-color: #333;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 20px 20px;
  box-sizing: border-box;

}

.side-menu-footer .avatar{
  width: 40px;
  height: 40px;
  overflow: hidden;  
  display: inline-block;
}
.side-menu-footer .avatar img{ 
  max-width: 100%;
  border-radius: 50%;
}

.side-menu-footer .user-info{
  display: inline-block;
  margin: 0 10px;
  color: white;
  position: absolute;
  opacity: 1;
  transition: opacity .20 ease-in;
}
.side-menu.inactive .side-menu-footer .user-info{
  opacity: 1;
  width: 0;
  height: 0;
  overflow: hidden;
  
}

.side-menu-footer .user-info h5{
  font-size: 20px;
}

.side-menu-footer .user-info p{
  font-size: 14px;
}
.devider {
  height: 1px;
  width: 100%;
  border-radius: 1px;
  background: #333;
}

.container {
  margin-left: 410px;
  transition: margin-left .2s ease-in;
  padding: 50px;
}
.container.inactive {
  margin-left: 80px;
}
.main-menu-name {
  padding-left: 10px;
  color: black;
  font-size: 20px;
  font-weight: 500;
}